

import React from 'react';


import style from './DialogItem.module.scss'
import ReadChecker from '../../../readCheker/ReadCheker'
import getMessageTime from '../../../../utils/getMessageTime';
import getAvatarColor from '../../../../utils/getAvatarColor';
import Avatar from '../../avatar/Avatar';

const color = 'linear-gradient(135deg, #00B3FF 0 %, #9CEEFF 100 %)'

function DialogItem({ message, unReaded, isMe }) {

    return (
        <div className={style.dialog__item}>

            <Avatar hash={message.user._id} avatar={message.user.avatar} online={message.user.online} />
            <div className={style.dialog__item_info}>
                <div>
                    <div className={style.dialog__item_title}>
                        {message.user.fullName}
                    </div>

                    <div className={style.dialog__item_lastMessage}>
                        {message.text}
                    </div>
                </div>
                <div>
                    <div className={style.dialog__item_date}>

                        {getMessageTime(message.date)}
                    </div>

                    {message && unReaded > 0
                        ? <div className={style.dialog__item_messageCount}>
                            {unReaded > 9 ? '+9' : unReaded}
                        </div>
                        : <div className={style.checked}>
                            <ReadChecker isMe={isMe} isReaded={message.isReaded} />
                        </div>}

                </div>
            </div>
        </div>
    )
}

export default DialogItem;
