import tinycolor from 'tinycolor'


export default hash => {
    // const color = 'linear-gradient(135deg, #00B3FF 0 %, #9CEEFF 100 %)'
    const [r, g, b] = hash.substr(0, 3).split('').map(char => char.charCodeAt() > 255 ? 255 : char.charCodeAt())

    return {
        color: tinycolor({ r, g, b }).toHexString(),
        colorLight: tinycolor({ r, g, b }).lighten(30).toHexString()
    }
}