import React from 'react';

import style from './Avatar.module.scss'
import getAvatarColor from '../../../utils/getAvatarColor';

function Avatar({ avatar, online, hash }) {

    if (avatar) {
        return <img src={avatar} className={style.dialog__item_avatar + ' ' + `${online && style.online}`} />
    }
    const [color1, color2] = getAvatarColor(hash)

    return (
        <div style={{ backgroundColor: `linear-gradient(135deg, ${color1} 0 %, ${color2} 100 %)` }} className={style.dialog__item_avatar + ' ' + `${online && style.online}`}></div>
    )
}

export default Avatar;
